.nav {
	background: -webkit-gradient(linear, left top, left bottom, from(rgba(40, 40, 40)), to(rgba(20, 20, 20)));
	background: -webkit-linear-gradient(rgba(40, 40, 40), rgba(20, 20, 20));
	background: -o-linear-gradient(rgba(40, 40, 40), rgba(20, 20, 20));
	background: linear-gradient(rgba(40, 40, 40), rgba(20, 20, 20));
  	position: relative;
}
.logo {
  	color: rgba(255, 100, 110);
  	font-family: raleway;
  	font-size: 30px;
  	font-weight: 700;
  	position: absolute;
  	top: 24%;
  	left: 2%;
  	text-shadow: 0px 0px 1px white;
}




